@tailwind base;
@tailwind components;
@tailwind utilities;
.text-content p {
  @apply py-4;
}
.text-content h1, .text-content h2, .text-content h3, .text-content h4 {
  @apply leading-tight;
  @apply py-3;
}
.text-content h1 {
  @apply text-4xl;
}
.text-content h2 {
  @apply text-3xl;
}
.text-content h3 {
  @apply text-2xl;
}
.text-content h4 {
  @apply text-xl;
}
.text-content p img {
  display: inline-block;
}
.text-content img.fr-dii.fr-fir {
  @apply float-right;
  @apply ml-10;
}
.text-content img.fr-dii.fr-fil {
  @apply float-left;
  @apply mr-10;
}
.text-content ul {
  padding-left: 30px;
}
.text-content ul li {
  list-style-type: circle;
}
.text-content img {
  max-width: 100%;
  height: auto !important;
}

.p-without-padding p {
  padding: 2px 0 !important;
}